import {_decorator, Component, instantiate, MeshRenderer, Node, Size} from 'cc';

const {ccclass, property} = _decorator;

@ccclass('Game_Board')
export class Game_Board extends Component {
  @property(Node) node: Node;
  @property(Node) origin: Node;
  @property(MeshRenderer) renderable: MeshRenderer;

  public init(size: Size) {
    for (let x = 0; x < size.width; x++) {
      for (let y = 0; y < size.height; y++) {
        let region = instantiate(board_region_prefab);
      }
    }
  }

  public showGrid() {}
}
